import { Injectable } from '@angular/core';
import { Http, Headers, Response } from '@angular/http';
import { map ,catchError} from 'rxjs/operators'; //radimo sa observables
import { throwError } from 'rxjs';
import { ErrorService } from '../services/error.service';
import {Rezervacija} from '../model/rezervacija.model';

@Injectable({
  providedIn: 'root'
})
export class RezervacijeService {

  rootUrl: String = '/';
  constructor(private http:Http,private errorService: ErrorService) { }

  getRezervacije(){

    const headers=new Headers({'Content-Type': 'application/json','Authorization': localStorage.getItem('token')});
    return this.http.get(this.rootUrl+ 'rezervacije/findspecified' ,{headers:headers}).pipe(
      map((response: Response) => {
        return response.json();
    }) ,catchError((error: Response) => {
      this.errorService.handleError(error);
      return throwError(error);
  })
  )
  }
  addRezervacija(rezervacija){
    const body=JSON.stringify(rezervacija); //pretvara objekat u string

    const headers=new Headers({'Content-Type': 'application/json','Authorization': localStorage.getItem('token')});
    return this.http.post(this.rootUrl+ 'rezervacije/add',body, { headers: headers }).pipe(map((response: Response) => response.json()),catchError((error: Response) => {
      this.errorService.handleError(error);
      console.log(error);
      return throwError(error);
  })
  );
  }
  getAllRezervacije(){

    const headers=new Headers({'Content-Type': 'application/json','Authorization': localStorage.getItem('token')});
    return this.http.get(this.rootUrl+ 'rezervacije/findAll',{headers:headers}).pipe(
      map((response: Response) => {
        return response.json();
    }) ,catchError((error: Response) => {
      this.errorService.handleError(error);
      return throwError(error);
  })
  )
  }
}