import { Injectable } from '@angular/core';
import {Prevoznik} from '../model/prevoznik.model';
import { Http, Headers, Response } from '@angular/http';
import { map ,catchError} from 'rxjs/operators'; //radimo sa observables
import { throwError } from 'rxjs';
import { ErrorService } from '../services/error.service';
import { BehaviorSubject } from 'rxjs';



@Injectable({
  providedIn: 'root'
})
export class PrevoznikService {
  rootUrl: String = '/';
  prevoznik: Prevoznik;
  constructor(private http:Http, private errorService:ErrorService) { }

  getPrevoznici(){
    return this.http.get(this.rootUrl+'prevoznik/get').pipe(
       map((response: Response) => {
          return response.json();
      }) ,catchError((error: Response) => {
        this.errorService.handleError(error);
        return throwError(error);
    })
);
  }
  addPrevoznici(prevoznik:Prevoznik){
    const body = JSON.stringify(prevoznik); //ono sto je korisnik uneo u formi, prebacujemo u json da bismo te podatke mogli poslati
    const headers = new Headers({'Content-Type': 'application/json','Authorization': localStorage.getItem('token') }); //u hederu navodimo da se radi o poruci koja sadrzi json
    return this.http.post(this.rootUrl+'prevoznik/add', body, { headers: headers }).pipe(map((response: Response) => response.json()),catchError((error: Response) => {
      this.errorService.handleError(error);
      console.log(error);
      return throwError(error);
  })
);
}
  deletePrev(prevoznik:Prevoznik){
    var body=JSON.stringify(prevoznik);
    const headers = new Headers({'Content-Type': 'application/json','Authorization': localStorage.getItem('token') });
            return this.http.post(this.rootUrl+'prevoznik/delete',body,{headers:headers}).pipe(
                map((response: Response) => {
                    return response;
                }) ,catchError((error: Response) => {
                  this.errorService.handleError(error);
                  return throwError(error);
              })
          );
  }
}
