import { Injectable } from '@angular/core';
import { User } from '../model/user.model';
import { Http, Headers, Response } from '@angular/http';
import { map ,catchError} from 'rxjs/operators'; //radimo sa observables
import { throwError } from 'rxjs';
import { ErrorService } from '../services/error.service';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  user: User;
  rootUrl: String = '/'; 
  private messageSource = new BehaviorSubject(0);
  currentMessage = this.messageSource.asObservable();
  //injektujemo http modul u konstruktor
  constructor(private http: Http, private errorService: ErrorService) {} 


  register(user: User) { //metoda za slanje zahteva za register, prosledjujemo korisnika kao parametar
    const body = JSON.stringify(user); //ono sto je korisnik uneo u formi, prebacujemo u json da bismo te podatke mogli poslati
    const headers = new Headers({'Content-Type': 'application/json'}); //u hederu navodimo da se radi o poruci koja sadrzi json
    return this.http.post(this.rootUrl+'user/register', body, { headers: headers }).pipe(map((response: Response) => response.json()),catchError((error: Response) => {
      this.errorService.handleError(error);
      return throwError(error);
  })
  //saljemo post zahtev na rutu localhost:3000/users/register , taj zahtev ima promenljivi body i zaglavlje u kome pise da se radi
  //o json objektu
  //odgovor "Response" pretvaramo u json i on ce stici u komponentu login, dok u slucaju da je doslo do greske
  //sifre se ne slazu, nalog ne postoji, onda ce te greske ici na errorService
);
}
changeMessage(user:number){
  this.messageSource.next(user);
}
 login(user:User){
   const body=JSON.stringify(user);
   const headers= new Headers({'Content-Type':'application/json'});
   return this.http.post(this.rootUrl+'user/login',body,{headers:headers}).pipe(map((response: Response)=>response.json()),catchError((error:Response)=>{
     this.errorService.handleError(error);
     return throwError(error);
   }))
 }

 isLoggedIn() {
   //proverava da li je token u local storage-u null ili ne(da li je neko prijavljen ili ne)
  return localStorage.getItem('token') !== null;
}
 //logout dodajemo samo u servis, i moras u navbaru podesiti za njega deo
 logout(){
   localStorage.clear(); //cistimo da bismo obrisali token
   this.changeMessage(1);

 }
}


