import { Injectable } from '@angular/core';
import {Ruta} from '../model/ruta.model';
import { Http, Headers, Response } from '@angular/http';
import { map ,catchError} from 'rxjs/operators'; //radimo sa observables
import { throwError } from 'rxjs';
import { ErrorService } from '../services/error.service';
import { BehaviorSubject } from 'rxjs';
import {Prevoznik} from '../model/prevoznik.model';
import { identifierName } from '@angular/compiler';


@Injectable({
  providedIn: 'root'
})
export class PutanjaService {
  rootUrl: String = '/';
  ruta:Ruta;
  prevoznik: Prevoznik;
  constructor(private http:Http, private errorService:ErrorService) { }

  //metoda za izlistavanje putanja 
  getPutanja(id: string){
    return this.http.get(this.rootUrl+'prevoznik/getputanja/'+id).pipe(
      map((response: Response)=>{
        return response.json();
      }) ,catchError((error: Response) => {
        this.errorService.handleError(error);
        return throwError(error);
    })

    )
  }
  //pronalazenje svih ruta
  getAllRoutes(params) {
    return this.http.get(this.rootUrl+'putanje/find/'+params).pipe(
      map((response: Response) => {
          return response.json();
      }) ,catchError((error: Response) => {
        this.errorService.handleError(error);
        return throwError(error);
    })
);
}


  //metoda za dodavanje putanje
  addPutanja(objekat:any){
    var body=JSON.stringify(objekat);
    //za sad samo ovo, posle ces dodavati za autorizaciju i tokene
    const headers = new Headers({'Content-Type': 'application/json','Authorization': localStorage.getItem('token')});
    return this.http.post(this.rootUrl+'prevoznik/addputanja/', body, { headers: headers }).pipe(
      map(
        (response: Response) => response.json()),
        catchError((error: Response) => {
      this.errorService.handleError(error);
      console.log(error);
      return throwError(error);
  })
);

  }

  deletePutanja(putanja:Ruta){
    var body=JSON.stringify(putanja);
    const headers = new Headers({'Content-Type': 'application/json','Authorization': localStorage.getItem('token') });
            return this.http.post(this.rootUrl+'putanje/delete',body,{headers:headers}).pipe(
                map((response: Response) => {
                    return response;
                }) ,catchError((error: Response) => {
                  this.errorService.handleError(error);
                  return throwError(error);
              })
          );
  }
}
