import { Component, OnInit } from '@angular/core';
import {Ruta} from '../../model/ruta.model';
import { Router, ActivatedRoute,ParamMap } from '@angular/router';
import {PutanjaService} from '../../services/putanja.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-add-putanja',
  templateUrl: './add-putanja.component.html',
  styleUrls: ['./add-putanja.component.css']
})
export class AddPutanjaComponent implements OnInit {

  form: FormGroup;
  public taken;
  constructor(private router:Router, private putanjaServ:PutanjaService,private toastr:ToastrService,private route: ActivatedRoute) { }

  ngOnInit() {
    this.form = new FormGroup({
      vremePolaska: new FormControl(null, [Validators.required]),
      ruta: new FormControl(null, [Validators.required,Validators.minLength(3)]),
      //uZemlji:new FormControl(null,[Validators.required])

    })
  }
  onSubmit(){
    const putanja=new Ruta(
      this.form.get('ruta').value.split(','),
      this.form.get('vremePolaska').value,
      //this.form.get('uZemlji').value,

    );
    //console.log(putanja);
    this.route.paramMap.subscribe((params:ParamMap) => {
      this.taken = params.get('id');
      var obj:any={};
      obj.prevoznik=this.taken;
      obj.putanja=putanja;
        this.putanjaServ.addPutanja(obj).subscribe(data=>{

          if(data.success==false) {
            this.toastr.error(data.msg, '',{
            closeButton:true,
            positionClass:'toast-bottom-left'
          });
          this.form.reset();
          } else{
           this.form.reset();
           this.toastr.success(data.msg, '',{
           closeButton:true,
           positionClass:'toast-bottom-left'
          });
          this.router.navigate(['/prevoznici/get']);
          }
    });

        
    })
  }

}
