import { Component, OnInit } from '@angular/core';
import {Ruta} from '../../model/ruta.model';
import { Router } from '@angular/router';
import {RezervacijeService} from '../../services/rezervacije.service';
import { ToastrService } from 'ngx-toastr';
import {Rezervacija} from '../../model/rezervacija.model';

@Component({
  selector: 'app-get-rezervacije',
  templateUrl: './get-rezervacije.component.html',
  styleUrls: ['./get-rezervacije.component.css']
})
export class GetRezervacijeComponent implements OnInit {

  public rezervacije:Rezervacija[];
  constructor(private router:Router, private rezervacijaServ:RezervacijeService) { }

  ngOnInit() {
    this.rezervacijaServ.getRezervacije().subscribe(data=>{
      this.rezervacije=data;
    });
  }
  //ovde eventualno jos neke metode ako bude potrebe..

}
