import { Component, OnInit } from '@angular/core';
import {Prevoznik} from '../../model/prevoznik.model';
import { Router } from '@angular/router';
import {PrevoznikService} from '../../services/prevoznik.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-get',
  templateUrl: './get.component.html',
  styleUrls: ['./get.component.css']
})
export class GetComponent implements OnInit {

  public isAdmin:Boolean;
  public prevoznici:Prevoznik[];
  constructor(private router:Router, private prevoznikServ:PrevoznikService,private toastr: ToastrService) { }

  ngOnInit() {
    localStorage.getItem('admin')=="1"?(this.isAdmin=true):(this.isAdmin=false);
    this.prevoznikServ.getPrevoznici().subscribe(data=>{
      this.prevoznici=data.obj;
    });
  }
  deletePrev(prevoznik){
    //brisanje sa sarvera
    this.prevoznikServ.deletePrev(prevoznik).subscribe();
    //brisanje iz niza za prikaz
    var index = this.prevoznici.indexOf(prevoznik);
    if (index > -1) {
      this.prevoznici.splice(index, 1);
    }
    this.toastr.success('Prevoznik je uspeno obrisan!','',{
      closeButton:true,
      positionClass:'toast-bottom-left'
    });
  }

  navigateTo(prevoznik){
    this.router.navigate(['/prevoznici/getputanja/'+prevoznik._id]);
  }
  addPrevoznik(){
    this.router.navigate(['/admin/addPrevoznik']);
  }

}
