import { Component, OnInit } from '@angular/core';
import {Prevoznik} from '../../model/prevoznik.model';
import { Router } from '@angular/router';
import {PrevoznikService} from '../../services/prevoznik.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-add-prevoznik',
  templateUrl: './add-prevoznik.component.html',
  styleUrls: ['./add-prevoznik.component.css']
})
export class AddPrevoznikComponent implements OnInit {

  form: FormGroup;
  constructor(private router:Router, private prevoznikServ:PrevoznikService,private toastr:ToastrService) { }

  ngOnInit() {
    this.form = new FormGroup({
      imeprevoznika: new FormControl(null, [Validators.required,Validators.minLength(1)]),
      kontakt: new FormControl(null, [Validators.required,Validators.minLength(5)]),

    })
  }
  onSubmit(){
    const prevoznik=new Prevoznik(
      this.form.value.imeprevoznika,
      this.form.value.kontakt
    );
    this.prevoznikServ.addPrevoznici(prevoznik).subscribe(data=>{

      if(data.success==false)
      {
        this.toastr.error(data.msg, '',{
          closeButton:true,
          positionClass:'toast-bottom-left'
        });
        this.form.reset();
      }else{
      this.form.reset();
      this.toastr.success(data.msg, '',{
        closeButton:true,
        positionClass:'toast-bottom-left'
     });
    this.router.navigate(['/prevoznici/get']);
    }
    });
  }

}
