import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {AddPrevoznikComponent} from './add-prevoznik/add-prevoznik.component';
import {AddPutanjaComponent} from './add-putanja/add-putanja.component';
import {GetallrezervacijeComponent} from './getallrezervacije/getallrezervacije.component';

const routes: Routes = [
  {path:'addPrevoznik',component:AddPrevoznikComponent},
  {path:'addputanja/:id',component:AddPutanjaComponent},
  {path:'getallrezervacije',component:GetallrezervacijeComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminRoutingModule { }
