import { Component, OnInit } from '@angular/core';
import {Ruta} from '../../model/ruta.model';
import { Router } from '@angular/router';
import {RezervacijeService} from '../../services/rezervacije.service';
import { ToastrService } from 'ngx-toastr';
import {Rezervacija} from '../../model/rezervacija.model';


@Component({
  selector: 'app-getallrezervacije',
  templateUrl: './getallrezervacije.component.html',
  styleUrls: ['./getallrezervacije.component.css']
})
export class GetallrezervacijeComponent implements OnInit {
  public rezervacije:Rezervacija[];

  constructor(private router:Router, private rezervacijaServ:RezervacijeService) { }

  ngOnInit() {
    this.rezervacijaServ.getAllRezervacije().subscribe(data=>{
      this.rezervacije=data;
    });
  }

}
