import { Component, OnInit } from '@angular/core';
//upravljanje formama i rutiranje
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import {User} from '../../model/user.model';
//uvodimo servis UserService i moras ga injektovati u konstruktor
import { UserService } from '../../services/user.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  form: FormGroup;
  constructor(private router: Router,private userService:UserService,private toastr:ToastrService) { }

  ngOnInit() {
    
    this.form = new FormGroup({

      name: new FormControl(null, [Validators.required,Validators.minLength(3)]),
    
      username: new FormControl(null, [Validators.required,Validators.minLength(5)]),

      email: new FormControl(null, [Validators.required,Validators.minLength(5),Validators.email]),

      password: new FormControl(null, [Validators.required,Validators.minLength(8)]),

      repeatpassword: new FormControl(null, [Validators.required]),
      
  });
  }
  onSubmit(){
    //kreiramo objekat
    const user=new User(
      this.form.value.email,
      this.form.value.password,
      this.form.value.name,
      this.form.value.username.toLowerCase(),

      
    );
    
    //RegisterUser
    //posto je observabla, moramo da se subscribujemo
    this.userService.register(user).subscribe(data=>{
    if(data.success==false)
    {
        this.toastr.error(data.msg, '',{
          closeButton:true,
          positionClass:'toast-top-center'
        });
        this.form.reset();
    }else{
    //resetujes inpute
    this.form.reset();
    this.toastr.success(data.msg, '',{
      closeButton:true,
      positionClass:'toast-top-center'
    });
    //saljemo korisnika da se loguje
    this.router.navigate(['/users/login']);
    }
    });
    
  }

}
