import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute,ParamMap } from '@angular/router';
import {PutanjaService} from '../../services/putanja.service';
import { ToastrService } from 'ngx-toastr';
import {Ruta} from '../../model/ruta.model';
import {RezervacijeService} from '../../services/rezervacije.service';


@Component({
  selector: 'app-getputanja',
  templateUrl: './getputanja.component.html',
  styleUrls: ['./getputanja.component.css']
})
export class GetputanjaComponent implements OnInit {

  public putanje:Ruta[];
  public taken; //parametar putanje

  public isAdmin:Boolean;
  constructor(private router:Router, private putanjaServ:PutanjaService,private route: ActivatedRoute,private rezervacijaServ:RezervacijeService,private toastr:ToastrService) { }

  ngOnInit() {

    localStorage.getItem('admin')=="1"?(this.isAdmin=true):(this.isAdmin=false);
    this.route.paramMap.subscribe((params:ParamMap) => {
      this.taken = params.get('id');
        this.putanjaServ.getPutanja(this.taken).subscribe(data=>{
        this.putanje=data.obj;
        for(let i=0;i<this.putanje.length;i++){
          for(let j=i+1;j<this.putanje.length;j++){
            const vreme1= this.putanje[i].vremePolaska.split(':')
            const sati1=parseInt(vreme1[0]);
            const minuti1=parseInt(vreme1[1]) + sati1*60;

            const vreme2= this.putanje[j].vremePolaska.split(':')
            const sati2=parseInt(vreme2[0]);
            const minuti2=parseInt(vreme2[1])+sati2*60;

            if(minuti1>minuti2){
              const m=this.putanje[i];
              this.putanje[i]=this.putanje[j];
              this.putanje[j]=m;
            }

          }
        }
        
        
    })
  })
  }

  deletePutanja(putanja){
    //brisanje sa sarvera
    this.putanjaServ.deletePutanja(putanja).subscribe();
    //brisanje iz niza za prikaz
    var index = this.putanje.indexOf(putanja);
    if (index > -1) {
      this.putanje.splice(index, 1);
    }
    this.toastr.success('Putanja je uspeno obrisana!','',{
      closeButton:true,
      positionClass:'toast-bottom-left'
    });
  }

  addPutanja(){
    this.router.navigate(['/admin/addputanja/'+this.taken]);
  }
}
