import { Component, OnInit } from '@angular/core';
import {Ruta} from '../../model/ruta.model';
import {PutanjaService} from '../../services/putanja.service';
import { Router } from '@angular/router';
import {RezervacijeService} from '../../services/rezervacije.service';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormControl, Validators,FormBuilder } from '@angular/forms';
import {IMyDpOptions} from 'mydatepicker';
import {Rezervacija} from '../../model/rezervacija.model';
import {UserService} from '../../services/user.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  public myDatePickerOptions: IMyDpOptions = {
    // other options...
    dateFormat: 'dd.mm.yyyy',
    inline: false,
    width: '80%'
};
  public model: any = { date: { year: 2018, month: 9, day: 8 } };


  rute:Ruta[];
  public isAdmin:Boolean;
  public found:Boolean; 


  constructor(private putanjaServ:PutanjaService,private router:Router,private rezervacijaServ:RezervacijeService,private toastr:ToastrService,private userService:UserService) { }
  goSearch(data){
    if(this.rute!=undefined){
      this.rute.length=0;
    }
    /*if(data.obj.length>0)
      this.found=true;
    else{
      this.found=false;
    }*/

      this.putanjaServ.getAllRoutes(data.target.value.toString()).subscribe(data => {
        //upisujemo ih u niz
        if(data.obj!=undefined){
          if(data.obj.length>0)
            this.found=true;
          else{
            this.found=false;
          }
        }
        this.rute=data.obj;
        //this.found=true;
        //sortiramo
        this.sortList();
      });
  }

  ngOnInit() {
    localStorage.getItem('admin')=="1"?(this.isAdmin=true):(this.isAdmin=false);
  }

  sortList(){
    for(let i=0;i<this.rute.length;i++){
      for(let j=i+1;j<this.rute.length;j++){
        const vreme1= this.rute[i].vremePolaska.split(':')
        const sati1=parseInt(vreme1[0]);
        const minuti1=parseInt(vreme1[1]) + sati1*60;

        const vreme2= this.rute[j].vremePolaska.split(':')
        const sati2=parseInt(vreme2[0]);
        const minuti2=parseInt(vreme2[1])+sati2*60;

        if(minuti1>minuti2){
          const m=this.rute[i];
          this.rute[i]=this.rute[j];
          this.rute[j]=m;
        }

      }
    }
  }

  rezervisi(ruta){
    var vreme=ruta.vremePolaska.split(':');
    //proveri vreme, mozda treba da dodas i gore i dole +1
    var sati=parseInt(vreme[0]);
    var minuta=parseInt(vreme[1]);
    console.log(vreme);
    const rezervacija:any={
      prevoznik:ruta.imePrevoznika,
      destinacija:ruta.ruta,

      datumPolaska:new Date(this.model.date.year,this.model.date.month-1,this.model.date.day).setHours(sati+2,minuta)
      
    };

    this.rezervacijaServ.addRezervacija(rezervacija).subscribe(data=>{
      if(data.success==false) {
        this.toastr.error(data.msg, '',{
        closeButton:true,
        positionClass:'toast-bottom-left'
      });
    }
    else{
      this.toastr.success("Reservation successful", '',{
        closeButton:true,
        positionClass:'toast-bottom-left'
       });
       this.router.navigate(['/users/getrezervacije']);
    }
    });
  }
  //provera da li je korisnik ulogovan
  isLoggedIn() {
    //ako je prijavljen vrati true..ako ne, false(i to se na kraju u htmlu namesta)
     if(this.userService.isLoggedIn())
     {
      return true;
     }else return false;
  }

}
